#validation-input {
  border: 2px solid #bdbdbd;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

#validation-input:focus {
  border: 2px solid #ffc94b;
  outline: none;
}

#validation-input.valid {
  border-color: #4caf50;
}

#validation-input.invalid {
  border-color: #f44336;
}

